<template>
    <div class="textarea" :class="[
        props.icon ? 'with-icon' : '',
        props.isError || props.error ? 'with-error' : ''
    ]">
        <div>
            <textarea
                v-bind:class="{
                    'without-placeholder': !props.placeholder,
                    clearable: props.clearable && !props.readonly,
                    info: !!props.info
                }"
                ref="textarea"
                :name="props.name"
                :maxlength="props.maxlength"
                :disabled="props.disabled"
                :placeholder="props.placeholder"
                :autocomplete="autocomplete"
                :readonly="props.readonly"
                @input="onInput"
                @keyup.enter="onPressEnter"
            />

            <span class="placeholder" :title="props.placeholder">{{ props.placeholder }}</span>

            <Icon v-if="props.icon" :icon="props.icon" />

            <div class="actions-block">
                <IconButton
                    v-if="props.clearable && !props.readonly"
                    tabindex="-1"
                    icon="close"
                    class="tertiary size-s clean"
                    @click="clean"
                />

                <Icon v-if="props.info" icon="help" class="icon-info" :title="props.info" />
            </div>
        </div>
        <span class="error">{{ props.error }}</span>
    </div>
</template>

<script setup>
import { onMounted, ref } from 'vue';
import { useI18n } from 'vue-i18n';
import IconButton from 'ðŸ”—/components/ui/IconButton.vue';

const props = defineProps({
    isError: { type: Boolean, default: false },
    icon: { type: String, default: '' },
    value: { type: [String, Number], default: '' },
    name: { type: [String, Number], default: '' },
    placeholder: { type: String, default: '' },
    maxlength: { type: [Number, null], default: null },
    disabled: { type: Boolean, default: false },
    error: { type: String, default: '' },
    autocomplete: { type: String, default: 'off' },
    readonly: { type: Boolean, default: false },
    clearable: { type: Boolean, default: true },
    info: { type: [String, Boolean], default: false },
    autofocus: { type: [String, Boolean], default: false }
});

const { t } = useI18n();

const textarea = ref(null);

const emit = defineEmits(['change', 'pressEnter']);

defineExpose({ textarea });

const clean = () => {
    textarea.value.value = '';
    setTimeout(() => {
        onInput({ target: textarea.value });
        textarea.value.focus();
    });
};

onMounted(() => {
    if (props.value !== null) {
        textarea.value.value = props.value;

        textarea.value.style.height = 'auto';
        textarea.value.style.height = `${textarea.value.scrollHeight}px`;
    }

    if (props.autofocus) {
        textarea.value.focus();
    }
});

const onPressEnter = (evn) => {
    onInput(evn);
    emit('pressEnter');
};

const onInput = (evn) => {
    evn.target.style.height = 'auto';
    evn.target.style.height = `${ evn.target.scrollHeight }px`;

    let value = evn.target.value.trimStart().replace(/\n{4,}/g, '\n\n\n');
    evn.target.value = value;
    emit('change', evn);
};
</script>

<style lang="scss" scoped>
div.textarea {
    display: flex;
    flex-direction: column;

    & > div {
        margin: 0 0 8px;
        display: flex;
        position: relative;
        width: 100%;
        max-width: var(--textarea-max-width);

        .placeholder {
            @include input-placeholder;
        }

        textarea {
            @include textarea;
            resize: none;

            &.clearable {
                padding-right: 40px;
                &.info {
                    padding-right: 80px;
                }
            }

            &.info {
                padding-right: 40px;
                &.clearable {
                    padding-right: 80px;
                }
            }
        }

        .actions-block {
            position: absolute;
            display: flex;
            align-items: flex-start;
            right: 8px;
            top: 8px;
            svg {
                margin: 0 8px;
            }
        }
 
        svg {
            &.icon-info {
                fill: var(--primary-80);
                stroke: var(--tertiary-500);
                width: 20px;
                height: 20px;
                cursor: pointer;

                &:hover {
                    fill: var(--primary-500);
                    stroke: var(--tertiary-500);
                }
            }
            &:not(.icon-info) {
                position: absolute;
                left: 16px;
                top: 50%;
                transform: translateY(-50%);
                width: 20px;
                height: 20px;
                fill: var(--dark-500);
                stroke: var(--dark-80);
            }
        }
    }

    &.with-icon {
        & > div {
            textarea {
                padding-left: 52px;
            }

            .placeholder {
                left: 53px;
            }
        }
    }

    & > .error {
        font: var(--font-label);
        color: var(--secondary-500) !important;
        // display: flex;
        min-height: 11px;
    }

    &.with-error {
        & > div {
            textarea {
                border-color: var(--secondary-500) !important;
            }

            textarea:not(:placeholder-shown) + .placeholder,
            textarea:focus + .placeholder {
                color: var(--secondary-500) !important;
            }

            textarea:not(:placeholder-shown) + .placeholder + svg,
            textarea:focus + .placeholder + svg {
                fill: var(--secondary-500) !important;
                stroke: var(--secondary-80) !important;
            }
        }
    }

    &.light {
        & > div {
            textarea {
                padding-top: 8px;
                padding-bottom: 8px;
            }

            svg {
                height: 18px;
                wigth: 18px;
            }

            textarea:not(:placeholder-shown) + .placeholder,
            textarea:focus + .placeholder {
                display: none;
            }
        }
    }

    &.with-placeholder {
        & > div {
            textarea {
                padding-top: 12px;
            }
            textarea:not(:placeholder-shown) + .placeholder,
            textarea:focus + .placeholder {
                display: flex;

                top: -6px;
                background: var(--white-500);
            }
        }
    }
    
    &.noerror {
        & > div {
            margin: 0;
        }
        & > .error {
            display: none;
        }
    }

    &.inside {
        textarea {
            border: none;
            background-color: var(--light-20);
        }
    }


    &.preview {
        textarea {
            overflow: auto;
            max-height: 280px;
        }
    }
}

div.textarea.rtl,
.direction-rtl div.textarea {
    & > div {
        .placeholder {
            left: auto;
            right: 20px;
        }

        textarea {
            &.clearable {
                padding-left: 40px;
                padding-right: 20px;
                &.info {
                    padding-left: 80px;
                    padding-right: 20px;
                }
            }

            &.info {
                padding-left: 40px;
                padding-right: 20px;
                &.clearable {
                    padding-left: 80px;
                    padding-right: 20px;
                }
            }
        }

        .actions-block {
            left: 8px;
            right: auto;
        }
 
        svg {
            &:not(.icon-info) {
                left: auto;
                right: 16px;
            }
        }
    }

    &.with-icon {
        & > div {
            textarea {
                padding-right: 52px;
                &:not(.clearable):not(.info) {
                    padding-left: 20px;
                }
            }

            .placeholder {
                left: auto;
                right: 53px;
            }
        }
    }
}
</style>
