<template>
    <div
        class="upload"
        :class="{ 'dropzone-upload': props.dropZone }"
        :style="{ 'background-image': previewSrc ? `url(${previewSrc})` : null }"
    >
        <input 
            type="file" 
            ref="fileInput" 
            style="display: none;"
            :accept="acceptedFormats.join(', ')" 
            :multiple="props.maxFiles !== 1"
            @change="handleFiles" 
        />

        <IconButton
            v-if="!props.dropZone"
            :icon="props.icon"
            class="size-m round upload"
            :loading="props.loading"
            @click="selectFile" />


        <div
            v-else
            class="dropzone"
            :class="[props.zoneType, data.over ? 'over' : '', previewSrc ? 'light' : '', props.loading ? 'loading' : '' ]"
            :style="{ width: props.zoneSize?.width, height: props.zoneSize?.height }"
            @click="selectFile"
            @dragover.prevent=""
            @dragenter.self="onDragEnter"
            @dragleave.self="onDragLeave"
            @drop.prevent="handleDrop"
        >
            <p :class="{ loading: props.loading }">
                {{ t(props.description) }}
                <Icon :icon="props.icon" />
            </p>

            <Button v-if="props.loading" :loading="true"
                class="tertiary size-l loader"
                :name="t(props.description)"
            />
        </div>
    </div>
</template>

<script setup>
    import { ref, reactive, computed } from 'vue';
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import { useI18n } from 'vue-i18n';
    import { message } from 'ðŸ”—/utils/utils';

    const { t } = useI18n();
    const fileInput = ref(null);
    const emit = defineEmits(['change']);

    const data = reactive({
        over: false,
        files: []
    });

    const selectFile = () => {
        if (props.disabled) {
            return;
        }

        if (props.loading) {
            return;
        }

        fileInput.value.click();
    };

    const props = defineProps({
        acceptedFormats: { type: Array, default: () => ([]) },
        maxSize: { type: Number, default: 50 * 1024 * 1024 },
        maxFiles: { type: Number, default: 1 },
        icon: { type: String, default: 'upload' },
        loading: { type: Boolean, default: false },
        dropZone: { type: Boolean, default: false },
        description: { type: String, default: 'ui.file-uploader.drop.description' },
        zoneType: { type: String, default: '' },
        zoneSize: { type: [Object, null], default: null },
        showPreview: { type: Boolean, default: false },
        disabled: { type: Boolean, default: false },
        preview: { type: [String, null], default: null }
    });

    const previewSrc = computed(() => {
        if (!props.showPreview || !(data.files.length || props.preview) || props.maxFiles !== 1) {
            return;
        }

        if (data.files?.length) {
            return URL.createObjectURL(data.files[0]);
        }

        if (props.preview) {
            return encodeURI(`${ import.meta.env.VITE_APP_FILES_URL }${ props.preview }`);
        }
    });

    const onDragEnter = () => data.over = true;
    const onDragLeave = () => data.over = false;

    const handleFiles = (e) => {
        data.over = false;
        if (!e?.target?.files?.length) {
            return;
        }

        const files = Array.from(e.target.files);
        handleFileChange(files);
    };

    const handleDrop = (e) => {
        const files = Array.from(e.dataTransfer.files);
        handleFileChange(files);
    };

    const handleFileChange = (files) => {
        if (props.disabled) {
            return;
        }

        data.over = false;

        if (!files.length) {
            return;
        }

        if (files.length > props.maxFiles) {
            message.info(t('ui.file-uploader.max-files', { msg: props.maxFiles }))
            return;
        }

        let error = false;
        const items = files.splice(0, props.maxFiles);

        const extensions = props.acceptedFormats.map(item => item.split('/')[1] || item);

        items.forEach(file => {
            // Ext
            const ext = file.name.split('.').pop().toLowerCase();

            if (!extensions.includes(ext)) {
                message.info(t('ui.file-uploader.format', { msg: extensions.join(', ') }));
                error = true;
                return;
            }

            // Size
            if (file.size > props.maxSize) {
                message.info(t('ui.file-uploader.size', { name: file.name, size: parseInt(props.maxSize / (1024 * 1024)) }))
                error = true;
                return;
            }
        });

        if (error) {
            return;
        }

        data.files = items;

        if (items.length > 1) {
            emit('change', items);
        } else {
            emit('change', items[0]);
        }
    }
</script>

<style lang="scss" scoped>
    .upload {
        background-size: cover;
        background-position: center;
        border-radius: 10px;
    }

    .dropzone {
        border: 2px dashed var(--gray-20);
        border-radius: 10px;
        background: transparent;//var(--white-20);
        transition: all .4s;
        padding: 16px;
        display: flex;
        flex-direction: row;
        align-items: center;
        cursor: pointer;
        position: relative;
        min-height: 100px;

        & > button.loader {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%);
            height: 72px;
            max-width: 120px;
        }

        & > p {
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            pointer-events: none;
            gap: 8px;
            font: var(--font-body-2);
            color: var(--gray-80);
            transition: all .2s;
            margin: 0;
            width: 100%;
            min-height: 48px;

            svg {
                width: 24px;
                height: 24px;
                fill: var(--gray-80);
                margin: 0 auto;
                transition: all .4s;
            }

            &.loading {
                opacity: 0;
                pointer-events: none;
                transition: none;
            }
        }

        &:hover {
            background: var(--white-80);
            border-color: var(--primary-500);
            & > p {
                color: var(--dark-500);
                svg {
                    fill: var(--dark-500);
                }
            }
        }

        &.over {
            opacity: 0.3;
        }

        &.light {
            & > p {
                opacity: 0;;
            }
            &:hover {
                & > p {
                    opacity: 1;
                }
            }
        }

        &.square-image {
            width: 180px;
            height: 180px;
        }


        &.loading {
            border: 2px dashed var(--gray-10);
            cursor: progress;

            &:hover {
                border: 2px dashed var(--gray-10);
            }
        }
    }
</style>
